import React, { memo, useCallback, useEffect, useState } from 'react';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from '@mui/material';
import { useHistory } from 'react-router-dom'

const TypesTable: React.FC<any> = memo(({ games, type }) => {
  const history = useHistory()

  const [list, setList] = useState<any>([])

  useEffect(() => {
    if (games?.length) {
      const arr = games.map((item: any) => {
        item.game.day_volume = item?.day_volume
        item.game.coin = item?.coin
        item.game.game_fi = item?.game_fi
        item.game.id = item?.id
        item.game.mkt_cap = item?.mkt_cap
        item.game.one_day = item?.one_day
        item.game.one_week = item?.one_week
        item.game.price = item?.price
        return item.game
      })
      setList(arr)
    }
  }, [games])

  const goDetails = useCallback((name: string) => {
    history.push(`/game/introduction?name=${name}&type=${type}`)
  }, [history, type])

  return (
    <TableContainer component={Paper} sx={{ ml: '0px' }}>
      <Table aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>#</TableCell>
            <TableCell>Game</TableCell>
            <TableCell>Token</TableCell>
            <TableCell>Price</TableCell>
            <TableCell>24h&nbsp;Volume</TableCell>
            <TableCell>Mkt&nbsp;Cap</TableCell>
            <TableCell>7D</TableCell>
            <TableCell>24H</TableCell>
            <TableCell>类型</TableCell>
            <TableCell>状态</TableCell>
          </TableRow>
        </TableHead>

        <TableBody>
          {list?.length && list?.map((row: any, index: number) => (
            <TableRow
              key={`${row?.id}=${index}`}
              hover
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              onClick={() => goDetails(row?.game_fi)}
            >
              <TableCell>{index + 1}</TableCell>
              <TableCell>{row?.game_fi || '-'}</TableCell>
              <TableCell>{row?.coin || '-'}</TableCell>
              {/* price */}
              <TableCell>{row?.price || '-'}<span></span></TableCell>
              <TableCell>{row?.day_volume || '-'}</TableCell>
              <TableCell>{row?.mkt_cap || '-'}</TableCell>
              <TableCell>{row?.one_day || '-'}</TableCell>
              <TableCell>{row?.one_week || '-'}</TableCell>
              <TableCell>{row?.class?.length ? row?.class[0]?.class : '-'}</TableCell>
              <TableCell>{(row?.status === 1 ? '测试中' : row?.status === 2 ? '正在运营' : row?.status === 3 ? '预售中' : '所有') || ''} </TableCell>
              {/* <TableCell 
            className={splitToNum(row?.one_day) > 0 ? 'up' : 'down'}
            >{splitToNum(row?.one_day) > 0 ? '+' : ''}{row?.one_day || '-'}</TableCell> */}
              {/* 类型 */}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
})

export default TypesTable