import React, { memo, useState, useEffect, useCallback } from 'react'
import { Container, Breadcrumbs, Link, Typography, Grid, Card, CardMedia, CardHeader, IconButton, Box } from '@mui/material';
import { Carousel } from 'antd';
import { useHistory } from 'react-router-dom'
import { inject, observer } from 'mobx-react'
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import Tooltip from '@mui/material/Tooltip';
import GEN_API from '@/api/fetch'
import './style.scss'

const University: React.FC<any> = observer(({ isMobile }: any) => {
  const history = useHistory()
  const [banners, setBanner] = useState<any>([])
  const [graphics, setGraphic] = useState<any>([])
  const [videos, setVideos] = useState<any>([])

  const Content = useCallback((list: any, title: string, href: string) => {
    return (
      <>
        <CardHeader
          disableTypography
          action={
            <IconButton href={href} size='small' sx={{ fontSize: '14px', fontWeight: 'bold', color: '#999999', mt: '8px', '&:hover': { color: '#FFF' } }} disableRipple disableFocusRipple>
              More
              <ChevronRightIcon />
            </IconButton>
          }
          title={<Typography variant="button" display="block" component='p' sx={{ fontSize: '28px', fontWeight: 'bold', color: '#FFF' }}>
            {title}
          </Typography>}
          sx={{ pl: '0px' }}
        />
        <Grid container spacing={4}>
          {list?.map((item: any) => (
            <Grid item key={item?.id} xs={6} sm={4} md={3}
              onClick={() => history.push(`/details?type=${+item.category?.parent_id}&cate_id=${item?.cate_id}&id=${item.id}`)}
            >
              <Card
                sx={{
                  borderRadius: { md: '10px', xs: '0.6rem' },
                  height: { md: "180px", xs: "6.2rem" },
                  display: 'flex', flexDirection: 'column', overflow: 'visible',
                  mt: '40px',
                  backgroundColor: '#26272B'
                }}
              >
                <CardMedia
                  component="img"
                  image={item?.cover}
                  alt="random"
                  sx={{
                    textAlign: 'center',
                    borderRadius: { md: '10px', xs: '0.6rem' },
                    width: '85%',
                    height: '80%',
                    mt: '-40px',
                    ml: 'auto',
                    mr: 'auto',
                    '&:hover': {
                      transform: 'scale(1.02) translateY(-6px)',
                      transition: 'all 0.2s linear',
                      borderRadius: '.75rem'
                    },
                    transition: 'all 0.2s linear'
                  }}
                />
                <Typography gutterBottom variant="h5" component="div" sx={{
                  color: '#FFF',
                  margin: { md: '14px 0 14px 6px', xs: '0.5rem 0 0.5rem 0.5rem' },
                  fontSize: { xs: '0.86rem', md: '1rem' },
                  pl: '5%',
                  overflow: 'hidden'
                }}
                >
                  {item.title.length > 25 ? <Tooltip title={item.title} placement="top">
                    <p>
                      {item?.title.slice(0, 24)}
                    </p>
                  </Tooltip> : item?.title}
                </Typography>
                <Typography sx={{ fontSize: { xs: '0.4rem', md: '0.68rem' }, pl: '8%', color: '#666666', pb: '5px' }} variant="h5" component="div">
                  {item.created.replace(/T|\+08:00/g, ' ')}
                </Typography>
              </Card>
            </Grid>
          ))}
        </Grid>
      </>
    )
  }, [history])

  useEffect(() => {
    (async () => {
      try {
        // 获取首页的教程
        const list = await GEN_API.fetCourse()
        setBanner(list?.data || [])
        const { data } = await GEN_API.fetGraphic()
        setGraphic(data)
        const res = await GEN_API.fetVideos()
        setVideos(res?.data || [])
      } catch (err) {
        console.log('错误信息', err)
      }
    })()
  }, [])

  return (
    <Container maxWidth="lg">
      <Breadcrumbs
        sx={{
          background: '#232427', borderRadius: { md: '12px', xs: '0.5rem' },
          padding: { md: '12px 24px', xs: '0.875rem 1.4rem' },
          margin: { md: '17px 0 24px', xs: '0.9rem 0 1.4rem' },
        }}
        separator={<NavigateNextIcon fontSize="small" />} aria-label="breadcrumb">
        [
        <Link key="1" href="/"
          sx={{ color: '#999', fontSize: { xs: '0..875rem' }, '&:hover': { color: '#999' } }}
        >Home</Link>,
        <Typography key="2" sx={{ color: '#FFF', fontSize: { xs: '0..875rem' } }}>
          BG University
        </Typography>,
        ];
      </Breadcrumbs>

      {/* Bnanner */}
      <Box component="div" className="banner-list">
        <Carousel
          className="banner"
          autoplay
          dots={isMobile ? true : false}
          infinite={true}
          variableWidth={true}
          slidesToShow={isMobile ? 1 : 3.5}
          slidesToScroll={1}
          autoplaySpeed={5000}
          arrows={true}
        >
          {
            banners?.slice(0, 4).map((item: any) => {
              return (<Link href={`/details?type=${+item.category?.parent_id}&cate_id=${item?.cate_id}&id=${item?.id}`} key={item?.id}>
                <img
                  src={item?.cover}
                  alt="img"
                />
              </Link>)
            })
          }
        </Carousel>
      </Box>

      {/*图文教程 */}
      {Content(graphics?.slice(0, 4) || [], '图文教程', '/graphic')}

      {/*视频教程*/}
      {Content(videos?.slice(0, 4) || [], '视频教程', '/video')}
    </Container>
  )
})

const mapStateToProps = ({ appStore: {
  isMobile,
} }: any) => {
  return {
    isMobile,
  }
}

export default inject(mapStateToProps)(memo(University));