import React, { memo, useEffect, useState } from 'react';
import Card from '@mui/material/Card';
import CardMedia from '@mui/material/CardMedia';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import { useHistory } from 'react-router-dom'
import GEN_API from '@/api/fetch'


const Like: React.FC<any> = memo(({ gameInfo }: any) => {
  const history = useHistory()
  const [list, setList] = useState<any>([])

  useEffect(() => {
    if (gameInfo?.label?.length) {
      (async () => {
        try {
          const { data } = await GEN_API.fetRelatedGames({ label_id: gameInfo.label[0].id, id: gameInfo.id })
          console.log('猜你喜欢的游戏', data)
          data?.length && data[0].game?.length ? setList(data[0].game?.slice(0, 4)) : setList([])
        } catch (err) {
          console.log('错误信息', err)
        }
      })()
    }
  }, [gameInfo])

  return (
    <>
      <Typography variant="button" display="block" component='div'
        sx={{
          margin: { md: '24px 0', xs: '2rem 0' },
          fontSize: { md: '28px', xs: '1.125rem' },
          fontWeight: 'bold', color: '#FFF'
        }}>
        猜您喜欢的游戏
      </Typography>
      <Grid container spacing={4}>
        {list?.map((item: any) => (
          <Grid item key={item?.id} xs={6} sm={4} md={3}
            onClick={() => history.push(`/game/introduction?id=${item.id}`)}
          >
            <Card
              sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
            >
              <CardMedia
                component="img"
                image={item?.cover}
                alt="random"
                sx={{ borderRadius: { md: '10px', xs: '0.6rem' } }}
              />
              <Typography gutterBottom variant="h5" component="div" sx={{
                color: '#FFF', margin: { md: '14px 0 14px 6px', xs: '1rem 0 1rem 0.5rem' },
                fontSize: { xs: '0.875rem' }
              }}>{item?.game_name} </Typography>
            </Card>
          </Grid>
        ))}
      </Grid>

    </>
  );
})

export default Like