import React from 'react';
import { Box } from "@mui/material";
import CardHeader from "@mui/material/CardHeader";
import Typography from "@mui/material/Typography";
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Card from "@mui/material/Card";
import BasicTable from "@/components/Leaderboard/Tables";
import CircularProgress from '@mui/material/CircularProgress';
import { useLightContext } from '@/hooks'

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}
function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ overflow: 'hidden', borderRadius: '0 0 .75rem .75rem' }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}
function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}
export default function Leaderboard() {
  const { tableInfo }: any = useLightContext()

  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  return (
    <>
      <Box maxWidth="lg" sx={{ display: { xs: 'block', md: 'block' } }}>
        <Card
          raised
          sx={{ bgcolor: 'transparent', backgroundImage: 'none', boxShadow: 'none', mb: '50px', overflow: 'visible' }}
        >
          <CardHeader
            disableTypography
            title={<Typography variant="button" display="block" component='p' sx={{ fontSize: { md: '28px', xs: '1.125rem' }, fontWeight: 'bold', color: '#FFF' }}>
              让我们去发现更多、更好的
            </Typography>}
            subheader={
              <Typography variant="button" display="block" component='span' sx={{ fontSize: '0.875rem', fontWeight: 'bold', color: '#999999' }}>
                在无尽的链游市场里数据繁杂冗长，我们为您整理好了！希望您可以借此探索出更多、更好的区块链游戏
              </Typography>
            }
            sx={{ pt: { xs: '2.5rem', md: '100px' }, pb: { xs: '1.25rem', md: '40px' }, pl: 0 }}
          />
          <Box sx={{ border: '1px solid rgba(255, 255, 255, 0.12)', borderRadius: '.75rem' }}>
            <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
              <Tabs value={value} onChange={handleChange} textColor='inherit' aria-label="basic tabs example">
                <Tab sx={{ lineHeight: '12px', fontSize: '1rem', fontWeight: 550, color: '#D8D8D8' }} label="Top Market Cap" {...a11yProps(0)} />
                <Tab sx={{ lineHeight: '12px', fontSize: '1rem', fontWeight: 550, color: '#D8D8D8' }} label="Top Gainers" {...a11yProps(1)} />
                <Tab sx={{ lineHeight: '12px', fontSize: '1rem', fontWeight: 550, color: '#D8D8D8' }} label="Top losers" {...a11yProps(2)} />
              </Tabs>
            </Box>
            <TabPanel value={value} index={0}>
              {
                tableInfo?.marketCap?.length ? <BasicTable info={tableInfo?.marketCap} type='marketCap' />
                  : <Box sx={{ display: 'flex', justifyContent: 'center', minHeight: { md: '300px' } }}> <CircularProgress sx={{ color: '#33DBE8', mt: { md: '100px' } }} /> </Box>
              }

            </TabPanel>
            <TabPanel value={value} index={1}>

              <BasicTable info={tableInfo?.gainers} />
            </TabPanel>
            <TabPanel value={value} index={2}>
              <BasicTable info={tableInfo?.losers} />
            </TabPanel>
          </Box>
        </Card>
      </Box>
    </>
  );
}

