import React, { memo } from 'react';
import ExpandLess from "@mui/icons-material/ExpandLess";
import ExpandMore from "@mui/icons-material/ExpandMore";
import IconButton from "@mui/material/IconButton";
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import MenuOpenIcon from "@mui/icons-material/MenuOpen";
import MenuIcon from "@mui/icons-material/Menu";
import ListItemIcon from "@mui/material/ListItemIcon";
import HomeIcon from "@mui/icons-material/Home";
import ListItemText from "@mui/material/ListItemText";
import List from "@mui/material/List";
import ListItemButton from "@mui/material/ListItemButton";
import SportsEsportsIcon from "@mui/icons-material/SportsEsports";
import Collapse from "@mui/material/Collapse";
import StarBorder from "@mui/icons-material/StarBorder";
import NewspaperIcon from "@mui/icons-material/Newspaper";
import SchoolIcon from "@mui/icons-material/School";
import { Link } from '@mui/material';

interface mobileMenuProps {
  title: string;
  subItem: ReadonlyArray<{
    title: string;
    href: string;
  }>;
}
const MobileMenu: React.FC<mobileMenuProps> = memo((props: mobileMenuProps) => {
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const [gameOpen, setGameOpen] = React.useState(true);
  const [rankOpen, setRankOpen] = React.useState(true);

  const handleGameClick = () => {
    setGameOpen(prevState => !prevState);
  };
  const handleRankClick = () => {
    setRankOpen(prevState => !prevState);
  };
  return (
    <React.Fragment>
      <IconButton
        onClick={handleClick}
        aria-controls={open ? 'account-menu' : undefined}
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        size="small"
        color="inherit"
        sx={{ display: { xs: 'block', md: 'none' } }}
        disableRipple
        disableFocusRipple
      >
        {open ? <MenuOpenIcon /> : <MenuIcon />}
      </IconButton>
      <Menu
        anchorEl={anchorEl}
        id="account-menu"
        open={open}
        onClose={handleClose}
        // onClick={handleClose}
        PaperProps={{
          elevation: 0,
          sx: {
            minWidth: '50%',
            overflow: 'visible',
            filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
            mt: 1.5,
            '& .MuiAvatar-root': {
              width: 32,
              height: 32,
              ml: -0.5,
              mr: 1,
            },
            '&:before': {
              content: '""',
              display: 'block',
              position: 'absolute',
              top: 0,
              right: 14,
              width: 10,
              height: 10,
              bgcolor: 'background.paper',
              transform: 'translateY(-50%) rotate(45deg)',
              zIndex: 0,
            },
          },
        }}
        transformOrigin={{ horizontal: 'right', vertical: 'top' }}
        anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
        sx={{ display: { xs: 'block', md: 'none' } }}
      >
        {/*Menu for Home*/}
        <Link href='/'>
          <MenuItem>
            <ListItemIcon><HomeIcon fontSize="small" /></ListItemIcon>
            <ListItemText primary="Home" className='href-name' />
          </MenuItem>
        </Link>
        {/*Menu for Game*/}
        <MenuItem>
          <List sx={{ ml: '-10%', pt: '0', pb: '0' }}>
            <ListItemButton onClick={handleGameClick} sx={{ pt: '0', pb: '0' }}>
              <ListItemIcon>
                <SportsEsportsIcon fontSize="small" />
              </ListItemIcon>
              <ListItemText primary="Game" />
              {gameOpen ? <ExpandLess /> : <ExpandMore />}
            </ListItemButton>
            <Collapse in={gameOpen} timeout="auto" unmountOnExit>
              <Link href={`/classify?type='games'`}>
                <List component="div" disablePadding>
                  <ListItemButton sx={{ pl: 4 }}>
                    <ListItemIcon>
                      <StarBorder />
                    </ListItemIcon>
                    <ListItemText primary="游戏分类" className='href-name' />
                  </ListItemButton>
                </List>
              </Link>

              <Link href='/guide'>
                <List component="div" disablePadding>
                  <ListItemButton sx={{ pl: 4 }}>
                    <ListItemIcon>
                      <StarBorder />
                    </ListItemIcon>
                    <ListItemText primary="Guide" className='href-name' />
                  </ListItemButton>
                </List>
              </Link>
            </Collapse>
          </List>
        </MenuItem>

        {/*Menu for Ranking*/}
        <MenuItem>
          <List sx={{ ml: '-10%', pt: '0', pb: '0' }}>
            <ListItemButton onClick={handleRankClick} sx={{ pt: '0', pb: '0' }}>
              <ListItemIcon>
                <SportsEsportsIcon fontSize="small" />
              </ListItemIcon>
              <ListItemText primary="Ranking" />
              {rankOpen ? <ExpandLess /> : <ExpandMore />}
            </ListItemButton>
            <Collapse in={rankOpen} timeout="auto" unmountOnExit>
              <Link href={`/classify?type='popular'`}>
                <List component="div" disablePadding>
                  <ListItemButton sx={{ pl: 4 }}>
                    <ListItemIcon>
                      <StarBorder />
                    </ListItemIcon>
                    <ListItemText primary="Popular Games" className='href-name' />
                  </ListItemButton>
                </List>
              </Link>

              <Link href='/latestGames'>
                <List component="div" disablePadding>
                  <ListItemButton sx={{ pl: 4 }}>
                    <ListItemIcon>
                      <StarBorder />
                    </ListItemIcon>
                    <ListItemText primary="Latest Games" className='href-name' />
                  </ListItemButton>
                </List>
              </Link>
            </Collapse>
          </List>
        </MenuItem>
        <Link href='/news'>
          <MenuItem>
            <ListItemIcon>
              <NewspaperIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText primary="News" className='href-name' />
          </MenuItem>
        </Link>
        <Link href='/university'>
          <MenuItem>
            <ListItemIcon>
              <SchoolIcon fontSize="small" />
            </ListItemIcon>
            <ListItemText primary="BG University" className='href-name' />
          </MenuItem>
        </Link>
      </Menu>
    </React.Fragment>
  );
})

export default MobileMenu