import React, { memo, useEffect, useState } from 'react'
import { Grid, Typography, Breadcrumbs, Link } from '@mui/material';
import { ArticleFirst, HotNews } from '@/components'
import { Pagination } from 'antd';
import GEN_API from '@/api/fetch'
import { ARTICLE_TYPE } from '@/config'
import NavigateNextIcon from '@mui/icons-material/NavigateNext';

const News: React.FC<any> = memo(() => {
  const [list, setList] = useState<any>([])
  const [curPage, setCurPage] = useState<number>(1)

  useEffect(() => {
    (async () => {
      try {
        const res = await GEN_API.queryArticle({ cate_id: ARTICLE_TYPE.HOT, page_size:8, page:curPage})
        setList(res)
        console.log('data-data', res)
      } catch (err) {
        console.log('错误信息', err)
      }
    })()
  }, [curPage])

  return (
    <main className="total-news">
      <Breadcrumbs
        sx={{
          background: '#232427', borderRadius: { md: '12px', xs: '0.5rem' },
          padding: { md: '12px 24px', xs: '0.875rem 1.4rem' },
          margin: { md: '17px 0 24px', xs: '0.9rem 0 1.4rem' },
        }}
        separator={<NavigateNextIcon fontSize="small" />} aria-label="breadcrumb">
        [
        <Link key="1" href="/"
          sx={{ color: '#999', fontSize: { xs: '0..875rem' }, '&:hover': { color: '#999' } }}
        >Home</Link>,
        <Typography key="2" sx={{ color: '#FFF', fontSize: { xs: '0..875rem' } }}>
          News
        </Typography>,
        ];
      </Breadcrumbs>
      <Grid container spacing={5} sx={{ mt: 3 }}>
        <Grid xs={12} md={8} sx={{ pr: { md: '16px' } }}>
          <Typography sx={{ fontSize: { md: '28px', xs: '1.125rem' }, fontWeight: 'bold', pb: '12px' }}>其他文章</Typography>
          <ArticleFirst list={list?.data || []} width={70} type='news' />
          <Pagination className='my-pagination' 
          defaultCurrent={1} 
          total={list?.article_num} 
          pageSize={8} 
          showQuickJumper
          hideOnSinglePage={true}
          onChange={(e:any)=>setCurPage(e)}/>
        </Grid>
        <Grid xs={12} md={4} >
          <Typography sx={{ fontSize: { md: '28px', xs: '1.125rem' }, fontWeight: 'bold' }}>24H热文</Typography>
          <HotNews list={list?.data?.filter(({ hot }: any) => hot !== 0).sort((a: any, b: any) => a.hot - b.hot) || []} /></Grid>
      </Grid>
    </main>
  )
})

export default News