import React, { memo, useCallback, useEffect, useState } from 'react';
import { Box, Grid, CardHeader, Typography, IconButton } from '@mui/material';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import { ArticleFirst, HotNews } from '@/components'
import GEN_API from '@/api/fetch'
import { ARTICLE_TYPE } from '@/config'

interface NewsProps {

}

const News: React.FC<NewsProps> = memo(() => {
  const [articles, setArticles] = useState<any>([])
  const [hotNews, setHotNews] = useState<any>([])

  const Icon = useCallback(() => {
    return (
      <Box component="span" sx={{ textAlign: 'right' }}>More
        <Box
          component="img"
          src={require('@/assets/images/icons/right-icon.svg')}
          sx={{
            display: { xs: 'none', md: 'inline-block' },
            marginLeft: '10px',
          }}
        />
      </Box>
    )
  }, [])

  // 了解世界上正在发生的
  useEffect(() => {
    (async () => {
      try {
        const { data } = await GEN_API.queryArticle({ cate_id: ARTICLE_TYPE.HAPPENING})
        setArticles(data || [])
      } catch (err) {
        console.log('错误信息', err)
      }
    })()
  }, [])

  // 获取24h热闻
  useEffect(() => {
    (async () => {
      try {
        const { data } = await GEN_API.queryArticle({ cate_id: ARTICLE_TYPE.HOT })
        if (data?.length) {
          let res = data.filter(({ hot }: any) => hot !== 0).sort((a: any, b: any) => a.hot - b.hot)
          setHotNews(res)
        } else {
          setHotNews([])
        }
      } catch (err) {
        console.log('错误信息', err)
      }
    })()
  }, [])

  return (
    <Box component='div'>
      <CardHeader
        disableTypography
        title={
          <Typography variant="button" display="block" component='p' sx={{ fontSize: { md: '28px', xs: '1.125rem' }, fontWeight: 'bold', color: '#FFF' }}>
            了解世界上正在发生的
          </Typography>
        }
        subheader={
          <Typography variant="button" display="block" component='span' sx={{
            fontSize: '0.875rem', fontWeight: 'bold', color: '#999999'
          }}>
            在数字货币的世界里每一分每一秒都在不断地发生新的事件，抓住每一个热点，得到每一个机遇。
          </Typography>
        }
        sx={{ pt: { xs: '2.5rem', md: '100px' }, pb: { xs: '1.25rem', md: '40px' }, pl: 0 }}
      />
      <Grid container spacing={5} sx={{ mt: 3 }}>
        <Grid xs={12} md={8} sx={{ pl: '40px', pt: 0 }}>
          <ArticleFirst list={articles?.slice(0, 4) || []} width={70} type='news' />
        </Grid>
        <Grid xs={12} md={4} sx={{ paddingLeft: '40px', marginTop: { xs: '0', md: '-75px' } }}>
          {typeof Icon !== 'undefined' && <IconButton href="/news" size='small' sx={{ display: { xs: 'none', md: 'flex' }, justifyContent: 'flex-end', fontSize: '0.875rem', fontWeight: 'bold', color: '#999', '&:hover': { color: '#FFF' } }} disableRipple disableFocusRipple>More<ChevronRightIcon /></IconButton>}
          <Typography noWrap sx={{ fontSize: { md: '15px', xs: '0.875rem' } }}>24H热闻</Typography>
          <HotNews list={hotNews} /></Grid>
      </Grid>
      <IconButton href="/news" size='small' sx={{ display: { xs: 'flex', md: 'none' }, width: '100%', justifyContent: 'flex-end', fontSize: '0.875rem', fontWeight: 'bold', color: '#999', '&:hover': { color: '#FFF' } }} disableRipple disableFocusRipple>
        More
        <ChevronRightIcon />
      </IconButton>
    </Box>
  );
})

export default News