import React, { memo, useCallback } from 'react';
import { CardMedia, Box, Typography } from '@mui/material';
// import { Pagination } from 'antd';
import { formatTime } from '@/utils'
import { useHistory } from 'react-router-dom'

interface listProps {
  list: any,
  width: number,
  type?: string
}

const ArticleFirst: React.FC<listProps> = memo(({ list, width, type }) => {
  const history = useHistory()

  const goPage = useCallback((item: any) => {
    switch (type) {
      case 'news':
        history.push(`/details?type='news'&cate_id=${item.cate_id}&id=${item.id}`);
        break;
        case 'ranking':
          history.push(`/details?type='ranking'&cate_id=${item.cate_id}&id=${item.id}`);
          break;
      case 'lastGames':
        history.push(`/game/introduction?id=${item.id}`)
        break;
    }
  }, [type, history])

  return (
    <>
      {
        list.map((item: any) => {
          return (
            <Box
              onClick={() => goPage(item)}
              sx={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'flex-start',
                padding: { md: '16px', xs: '0.6rem' },
                background: { md: 'transparent', xs: '#26272B' },
                border: '1px solid #3D3E45',
                borderRadius: { md: '12px', xs: '0.5rem' },
                marginBottom: { md: '16px', xs: '0.8rem' },
                '&:hover': {md:{
                  background: '#26272B',
                  transform: 'scale(1.02) translateY(-6px)',
                  boxShadow: '0 16px 30px -14px #000000ab',
                  transition: 'all 0.2s linear',
                  borderRadius: '.75rem'}
                },
                transition: 'all 0.2s linear'
              }}
            >
              <CardMedia
                component="img"
                sx={{
                  width: { md: `calc(100% - ${width}% - 16px)`, xs: '7.875rem' },
                  height: { md: '7.2rem', xs: '6rem' },
                  marginRight: '1.5rem',
                  borderRadius: '.75rem'
                }}
                image={item?.cover}
                alt=""
              />
              <Box sx={{
                height: { md: '7.2rem', xs: '6rem' },
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-between',
                alignItems: 'flex-start',
                maxWidth: { xs: '100%', md: `${width}%` },
                width: '100%'
              }}>
                <Typography noWrap sx={{ display: { xs: 'none', md: 'block' }, fontSize: { md: '18px', xs: '0.875rem' }, maxWidth: '100%', fontWeight: 'bold', color: '#FFF' }}>{item?.title}</Typography>
                <Box sx={{
                  display: { xs: '-webkit-box', md: 'none' }, WebkitLineClamp: '2',
                  WebkitBoxOrient: 'vertical', overflow: 'hidden', padding: 0,
                  color: '#FFF'
                }}>{item?.title}</Box>
                <Box sx={{
                  display: { xs: 'none', md: '-webkit-box' }, WebkitLineClamp: '2',
                  fontSize: '0.8125rem', color: '#999999',
                  WebkitBoxOrient: 'vertical', overflow: 'hidden', padding: 0
                }}>{item?.summary}</Box>

                {
                  type === 'lastGames' ?
                    <Box sx={{ width: '100%', display: { md: 'flex', xs: 'block' }, flexWrap: 'nowrap', justifyContent: 'space-between', mt: { md: '20px' } }}>
                      <Box sx={{display:'flex', flexWrap:'nowrap'}}>
                        {
                          item?.label?.map(({ word }: any) => {
                            return <Box key={word} sx={{
                              border: "1px solid #999",
                              width: { md: '43px', xs: '2.2rem' },
                              textAlign: 'center',
                              height: { md: '22px', xs: '1.5rem' },
                              lineHeight: { md: '22px', xs: '1.5rem' },
                              fontSize: { md: '12px', xs: '0.875rem' },
                              color: '#999',
                              borderRadius: '4px',
                              mr: { md: '10px', xs: '0.5rem' },
                              mb: { xs: '0.8rem' }
                            }}>{word}</Box>
                          })
                        }
                      </Box>
                      <Typography noWrap sx={{
                        fontSize: '0.8125rem', color: '#FFF', textAlign: 'center', height: '28px', lineHeight: '28px', padding: { md: '0 38px' },
                        background: '#101012', borderRadius: '4px'
                      }}>上线时间：{item?.created ? formatTime(item?.created) : ''}</Typography>
                    </Box>
                    : <Typography noWrap sx={{
                      fontSize: '0.8125rem',
                      color: '#999999'
                    }}>{item?.created ? formatTime(item?.created) : ''}</Typography>
                }
              </Box>
            </Box>
          )
        })
      }
    </>
  );
})

export default ArticleFirst