import React, { useMemo, memo, useCallback, useLayoutEffect, useRef } from 'react';
import Container from '@mui/material/Container';
import CssBaseline from '@mui/material/CssBaseline';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import { Header, Footer } from '@/components';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { inject, Observer } from 'mobx-react';
import routes from './routes'
import { isMobileAgent } from '@/utils'
import './App.scss';

const App: React.FC<any> = memo(({ isMobile, setIsMobile }) => {

  const timerResize: any = useRef()
  const theme = createTheme({
    palette: {
      mode: 'dark',
      primary: {
        main: '#1D1E21',
      },
    },
  });

  const sections = useMemo(() => [
    { title: 'Technology', url: '#' },
    { title: 'Design', url: '#' },
    { title: 'Culture', url: '#' },
    { title: 'Business', url: '#' },
    { title: 'Politics', url: '#' },
    { title: 'Opinion', url: '#' },
    { title: 'Science', url: '#' },
    { title: 'Health', url: '#' },
    { title: 'Style', url: '#' },
    { title: 'Travel', url: '#' },
  ], [])


  const checkUserAgent = useCallback(() => {
    const isMobile = isMobileAgent()
    setIsMobile(isMobile)
  }, [setIsMobile])

  useLayoutEffect(() => {
    checkUserAgent()
    window.addEventListener('resize', () => {
      clearTimeout(timerResize.current)
      timerResize.current = setTimeout(() => {
        clearTimeout(timerResize.current)
        checkUserAgent()
      }, 300)
    })
  }, [checkUserAgent])

  return (
    <Router>
      <Observer>
        {
          () => <>
            {
              typeof isMobile === 'boolean' ? <div>
                <ThemeProvider theme={theme}>
                  <CssBaseline />
                  <Header title="" sections={sections} />
                  <Container maxWidth="lg">
                    <Switch>
                      {
                        routes.map((route) => {
                          return (
                            <Route
                              exact
                              key={route.path as string}
                              {...route}
                            />
                          )
                        })
                      }
                    </Switch>
                  </Container>
                  <Footer />
                </ThemeProvider>

              </div> : <></>
            }
          </>
        }
      </Observer>
    </Router>
  )
})

const mapStateToProps = ({ appStore: {
  isMobile,
  setIsMobile,
} }: any) => {
  return {
    isMobile,
    setIsMobile,
  }
}

export default inject(mapStateToProps)(App);
