import React, { memo, useCallback, useEffect, useState} from 'react'
import {  Observer } from 'mobx-react'
import { LightContext } from '@/hooks'
import GEN_API from '@/api/fetch'

const Context: React.FC<any> = memo(({children}) => {
  const [banner, setBanner] = useState<any>([])
  const [tableInfo, setTableInfo]= useState<any>([])

  const fetchBanner = useCallback(async()=>{
    try{
      const {data} = await GEN_API.getBanner()
      setBanner(data || [])
    }catch(err){
      console.log('错误信息',err)
    }
  }, [])

  const fetchTableInfo = useCallback(async()=>{
  try{
    const marketCap = await GEN_API.getTopMarketCap()
    const gainers = await GEN_API.getTopGainers()
    const losers = await GEN_API.getTopLosers()
    setTableInfo({
      marketCap: marketCap?.data || [],
      gainers: gainers?.data || [],
      losers: losers?.data || [],
    })
  }catch(error){
    console.log('错误信息', error)
  }
  }, [])

  useEffect(()=>{
    fetchBanner()
    fetchTableInfo()
  }, [fetchBanner, fetchTableInfo])

  return (
    <Observer>
      {
        () =>
          <LightContext.Provider value={{
            banner,
            tableInfo
          }}>
          {children}
          </LightContext.Provider>
      }
    </Observer>
  )
})
export default Context