import DayJs from 'dayjs'

export const isMobileAgent = (): boolean => {
  if (/(iPhone|iPad|iPod|iOS|Android|Mobile)/i.test(navigator.userAgent)) {
    return true
  } else {
    return false
  }
}

export const splitToNum = (amount?: any): number => {
  return parseFloat(amount || 0)
}

export function formatTime(time: number | string | Date, template = 'YYYY-MM-DD HH:mm:ss') {
  if ('number' === typeof time && String(time).length === 10) {
    time = time * 1000
  }
  return DayJs(time).format(template)
}

export const parseSearch = (search: string) => {
  if (search) {
    const obj: any = {}
    const parseArr = search.substring(1)?.split('&')
    parseArr.forEach((item: string) => {
      const parseItem = item?.split('=')
      obj[parseItem[0]] = parseItem[1]
    })
    return obj
  }
  return {}
}

export const thousands2 = (num: number) => {
  const str = num.toString();
  const reg = str.indexOf('.') > -1 ? /\B(?=(\d{3})+\.)/g : /\B(?=(\d{3})+$)/g;
  return str.replace(reg, ',');
}

