import { observable, action } from 'mobx'

class appStore {
  // constructor() {
  //   // 添加makeObservable
  //   makeObservable(this)
  // }
  @observable isMobile?: boolean
  @observable ipLocale?: string = ''

  @action
  setIsMobile = (isMobile?: boolean) => {
    if (isMobile !== this.isMobile) {
      this.isMobile = isMobile
    }
  }

  @action
  setIpLocale = (locale?: string) => {
    if (locale !== this.ipLocale) {
      this.ipLocale = locale
    }
  }

}
export default new appStore()