import React, { memo, useCallback, useEffect } from 'react';
import {Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper} from '@mui/material';
import { splitToNum } from '@/utils'
import { useHistory } from 'react-router-dom'

const BasicTable: React.FC<any> = memo(({ info, type }) => {
  const history = useHistory()
  
  const goDetails = useCallback((name: string) => {
    history.push(`/game/introduction?name=${name}`)
  }, [history])

  useEffect(()=>{
    console.log('info', info)
  }, [info])


  return (
    <TableContainer component={Paper} sx={{ ml: '0px' }}>
      <Table aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>#</TableCell>
            {type === 'marketCap' && <TableCell>Game</TableCell>}
            {type !== 'marketCap' && <TableCell>Name</TableCell>}
            <TableCell>Coin</TableCell>
            <TableCell>Price</TableCell>
            <TableCell>24H</TableCell>
            {type === 'marketCap' && <TableCell>7D</TableCell>}
            <TableCell align={type === 'marketCap' ? 'center' : "right"}>24h&nbsp;Volume</TableCell>
            {type === 'marketCap' && <TableCell align="right">Mkt&nbsp;Cap</TableCell>}
          </TableRow>
        </TableHead>
        <TableBody sx={{minHeight:{md:'100px'}}}>
          {info?.length && info?.slice(0, 10)?.map((row: any, index: number) => (
            <TableRow
              key={`${row?.coin}=${index}`}
              hover
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              onClick={() => goDetails(row?.game_fi)}
            >
              <TableCell>{index + 1}</TableCell>
              {type === 'marketCap' && <TableCell>{row?.game_fi || '-'}</TableCell>}
              {type !== 'marketCap' && <TableCell>{row?.game_fi || '-'}</TableCell>}
              <TableCell>{row?.coin || '-'}</TableCell>
              <TableCell>{row?.price || '-'}</TableCell>
              <TableCell 
              className={splitToNum(row?.one_day) > 0 ? 'up' : 'down'}
              >{splitToNum(row?.one_day) > 0 ? '+' : ''}{row?.one_day || '-'}</TableCell>
              {type === 'marketCap' && <TableCell className={splitToNum(row?.one_week) > 0 ? 'up' : 'down'}>{row?.one_week || '-'}</TableCell>}
              <TableCell align={type === 'marketCap' ? 'center' : "right"}
              >{row?.day_volume || '-'}</TableCell>
              {type === 'marketCap' && <TableCell align="right">{row?.mkt_cap || '-'}</TableCell>}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
})

export default BasicTable
