import React, { memo, useEffect, useState } from 'react';
import { Grid, Typography, IconButton, CardHeader } from "@mui/material";
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import { ArticleFirst } from '@/components'
import GEN_API from '@/api/fetch'
import { ARTICLE_TYPE } from '@/config'

interface PotentialPostProps {

}

const Potential: React.FC<PotentialPostProps> = memo(() => {

  const [potentials, setPotentials] = useState<any>([])

  useEffect(() => {
    (async () => {
      try {
        const { data } = await GEN_API.queryArticle({ cate_id: ARTICLE_TYPE.POTENTIAL })
        setPotentials(data || [])
      } catch (err) {
        console.log('错误信息', err)
      }
    })()
  }, [])

  return (
    <>
      <CardHeader
        disableTypography
        action={
          <IconButton href="/latestGames" size='small' sx={{ display: { md: 'flex', xs: "none" }, fontSize: '14px', fontWeight: 'bold', color: '#999999', mt: '8px', '&:hover': { color: '#FFF' } }} disableRipple disableFocusRipple>
            More
            <ChevronRightIcon />
          </IconButton>
        }
        title={<Typography variant="button" display="block" component='p' sx={{ fontSize: { md: '28px', xs: '1.125rem' }, fontWeight: 'bold', color: '#FFF' }}>
          找到每一个“潜力股”
        </Typography>}
        subheader={
          <Typography variant="button" display="block" component='span' sx={{
            fontSize: '0.875rem', fontWeight: 'bold', color: '#999999'
          }}>
            每当出现一个新的区块链游戏或许可能是一个“暴富”的机会
          </Typography>
        }
        sx={{ pt: { xs: '2.5rem', md: '100px' }, pb: { xs: '1.25rem', md: '40px' }, pl: 0 }}
      />
      <Grid container spacing={2}>
        <Grid item xs={12} md={6}>
          <ArticleFirst list={potentials?.slice(0, 2)} width={66} type='ranking' />
        </Grid>
        <Grid item xs={12} md={6}>
          <ArticleFirst list={potentials?.slice(2, 4)} width={66} type='ranking' />
        </Grid>
      </Grid>
      <IconButton href='/latestGames' size='small' sx={{ display: { md: 'none', xs: "flex" }, width: '100%', justifyContent: 'flex-end', fontSize: '0.875rem', fontWeight: 'bold', color: '#999', '&:hover': { color: '#FFF' } }} disableRipple disableFocusRipple>
        More
        <ChevronRightIcon />
      </IconButton>
    </>
  );
})

export default Potential

