import React, { memo, useCallback } from 'react';
import { formatTime } from '@/utils'
import { withRouter, useHistory } from 'react-router-dom'
import './style.scss'


const HotNews: React.FC<any> = memo(({ list }: any) => {
  const history = useHistory()

  const goDetails = useCallback((article: any) => {
    history.push(`/details?type='news'&cate_id=${article.cate_id}&id=${article.id}`)
  }, [history])

  return (
    <div className="hotNews">
      <div className="box">
        {
          list?.map((item: any, index: number) => {
            return (
              <div key={item?.id} onClick={() => goDetails(item)}>
                <div key={index} className="item-container">
                  {/* 坐标的图标 */}
                  <div className="rank-icon">{ index + 1}</div>
                  {/* 右边的文章内容 */}
                  <div className="item">
                    <p>{item?.title}</p>
                    <span>{item?.created ? formatTime(item?.created) : ''}</span>
                  </div>
                </div>
              </div>
            )
          })
        }
      </div>
    </div>
  );
})


export default withRouter(HotNews)