import React, { memo, useEffect, useState } from 'react'
import { Card, Grid, CardMedia, CardContent, Typography, Box, Breadcrumbs, Link } from '@mui/material';
import { Pagination } from 'antd';
import { ArticleFirst } from '@/components'
import { formatTime } from '@/utils'
import { ARTICLE_TYPE } from '@/config'
import GEN_API from '@/api/fetch'
import NavigateNextIcon from '@mui/icons-material/NavigateNext';


const LatestGames: React.FC<any> = memo(({ isMobile }) => {
  const [potentials, setPotentials] = useState<any>([])
  const [games, setGames] = useState<any>([])
  const [curPage, setCurPage] = useState<number>(1)

  // 获取文章列表
  useEffect(() => {
    (async () => {
      try {
        const {data} = await GEN_API.queryArticle({ cate_id: ARTICLE_TYPE.POTENTIAL })
        setPotentials(data || [])
      } catch (err) {
        console.log('错误信息', err)
      }
    })()
  }, [])

  // 获取游戏
  useEffect(() => {
    (async () => {
      try {
        const res = await GEN_API.fetgGameDetails({page_size:8, page:curPage})
        setGames(res)
      } catch (err) {
        console.log('错误信息', err)
      }
    })()
  }, [curPage])


  return (
    <main className="total-news">
      <Breadcrumbs
        sx={{
          background: '#232427', borderRadius: { md: '12px', xs: '0.5rem' },
          padding: { md: '12px 24px', xs: '0.875rem 1.4rem' },
          margin: { md: '17px 0 24px', xs: '0.9rem 0 1.4rem' },
        }}
        separator={<NavigateNextIcon fontSize="small" />} aria-label="breadcrumb">
        [
        <Link key="1" href="/"
          sx={{ color: '#999', fontSize: { xs: '0..875rem' }, '&:hover': { color: '#999' } }}
        >Home</Link>,
        <Typography key="2" sx={{ color: '#FFF', fontSize: { xs: '0..875rem' } }}>
          Latest Games
        </Typography>,
        ];
      </Breadcrumbs>
      <Grid container spacing={5} sx={{ mt: 3 }}>
        <Grid xs={12} md={8} sx={{ pr: { md: '16px' } }}>
          <Typography sx={{ fontSize: { md: '28px', xs: '1.125rem' }, fontWeight: 'bold', pb: '12px' }}>Latest Games</Typography>
          <ArticleFirst list={games?.data || []} width={70} type='lastGames' />
          <Pagination className='my-pagination' 
          defaultCurrent={1} 
          total={games?.article_num} 
          pageSize={8} 
          showQuickJumper
          hideOnSinglePage={true}
          onChange={(e:any)=>setCurPage(e)}/>
        </Grid>
        <Grid xs={12} md={4}>
          <Typography sx={{ fontSize: { md: '28px', xs: '1.125rem' }, fontWeight: 'bold', pb: { xs: '12px' } }}>Coming Games</Typography>
          <Box sx={{ mt: { md: '12px', xs: 0 } }}>
            {
              potentials?.slice(0, 5)?.map(({ cover, title, created, cate_id, id }: any) => {
                return <Card
                  component='a'
                  href={`/details?type='ranking'&cate_id=${cate_id}&id=${id}`}
                  className='text-container'
                  key={created}
                  sx={{
                    p: { md: '16px', xs: '1rem' },
                    '&:hover': {
                      background: '#26272B',
                    }
                  }} >
                  <CardMedia
                    component="img"
                    image={cover}
                    alt="avg"
                    sx={{ width: '5rem', height: '5rem', borderRadius: { md: '12px', xs: '0.5rem' } }}
                  />
                  <CardContent sx={{ maxWidth: 'calc(100% - 5rem - 16px)', width: '100%', padding: '0px' }}>
                    <Typography className='title'>{title}</Typography>
                    <Typography noWrap sx={{ fontSize: '0.8125rem', color: '#999999', }}>{created ? formatTime(created) : ''}</Typography>
                  </CardContent>
                </Card>
              })
            }
          </Box>
        </Grid>
      </Grid>
    </main>
  )
})

export default LatestGames