import React, { memo, useCallback, useEffect, useState } from 'react'
import { Container, Grid, Card, CardMedia, CardContent, Box, Typography, Link, Divider, Breadcrumbs } from '@mui/material';
import { parseSearch, formatTime, thousands2 } from '@/utils'
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import Avatar from '@mui/material/Avatar';
import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';
import { withRouter, useHistory } from 'react-router-dom'
import { Community } from '@/components'
import GEN_API from '@/api/fetch'
import TableInfo from './TableInfo'
import Like from './Like'
import './style.scss'

const GameIntroduction: React.FC<any> = memo(({ location: { search } }) => {
  const [gameInfo, setGameInfo] = useState<any>({})
  const history = useHistory()

  const fetchDetails = useCallback(async (params: string) => {
    let res: any = []
    try {
      if (typeof params === 'number') {
        const { data } = await GEN_API.fetgGameDetails({ id: +params })
        res = data
      } else {
        const { data } = await GEN_API.fetgGameDetails({ game_fi: decodeURI(params) })
        res = data
      }
      res?.length ? setGameInfo(res[0]) : setGameInfo({})
    } catch (err) {
      console.log('错误信息', err)
    }
  }, [])

  // 具体的有些介绍
  useEffect(() => {
    const params: any = parseSearch(search)?.name ? parseSearch(search).name : (+parseSearch(search)?.id)
    fetchDetails(params)
  }, [search, fetchDetails, history])

  return (
    <Container maxWidth="lg"
      sx={{
        mt: { md: '35px', xs: '2rem' },
        padding: 0
      }}
      className="games-details">
      <Breadcrumbs
        sx={{
          background: '#232427', borderRadius: { md: '12px', xs: '0.5rem' },
          padding: { md: '12px 24px', xs: '0.875rem 1.4rem' },
          margin: { md: '17px 0 24px', xs: '0.9rem 0 1.4rem' },
        }}
        separator={<NavigateNextIcon fontSize="small" />} aria-label="breadcrumb">
        [
        <Link key="1" href="/"
          sx={{ color: '#999', fontSize: { xs: '0..875rem' }, '&:hover': { color: '#999' } }}
        >Home
        </Link>,
        <Link
          key="2"
          sx={{ color: '#999', fontSize: { xs: '0..875rem' }, '&:hover': { color: '#999' } }}
          href={`/classify?type=${parseSearch(search)?.type?.includes('games') ? 'games' : 'popular'}`}
        >
          {parseSearch(search)?.type?.includes('games') ? '游戏分类' : 'Popular Games'}
        </Link>,
        <Typography key="3" sx={{ color: '#FFF', fontSize: { xs: '0..875rem' } }}>
          {gameInfo?.title}
        </Typography>,
        ];
      </Breadcrumbs>
      <>
        <Grid container spacing={5}
          sx={{
            mt: { md: 3, xs: 0 },
            width: { xs: '100%' },
            margin: { xs: 0 },
            padding: { md: '24px', xs: '0 0 35px 0' }
          }}
          className='games-details-card'
        >
          <Grid item={true} xs={12} md={2} sx={{ pr: { md: '16px' } }}>
            <CardMedia
              component="img"
              sx={{ width: { md: '100%', xs: "120px" }, mt: { xs: "10px" }, display: { xs: 'inline-block', sm: 'block' } }}
              image={gameInfo.cover}
              alt={gameInfo.imageLabel}
            />
          </Grid>
          <Grid xs={12} md={10}>
            <CardContent sx={{ flex: 1, ml: { md: '37px', xs: 0 } }} className="content">
              {/* title && 标签 */}
              <Box className="title-label">
                <Box sx={{ color: '#FFF', fontSize: { md: '24px', xs: '1.25rem' }, fontWeight: 'bold', lineHeight: 1 }}>{gameInfo.title}</Box>
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignContent: "center" }}>
                  {
                    gameInfo?.label?.map(({ word }: any, index: number) => {
                      return <Box
                        key={index}
                        sx={{
                          color: '#CCC', borderRadius: { md: '8px', xs: '0.5rem' },
                          fontSize: { xs: '0.875rem' },
                          border: '1px solid #999', fontWeight: 'bold', textAlign: 'center', ml: { md: 2, xs: 1 },
                          padding: { md: '10px 14px', xs: '0.4rem 0.6rem' }
                        }}>{word}</Box>
                    })
                  }
                </Box>
              </Box>
              {/* 游戏介绍 */}
              <Box sx={{ color: '#999', fontSize: { xs: '0.875rem' } }}>{gameInfo.summary}</Box>
              {/* 链名称 */}
              <Stack direction="row" spacing={1} divider={<Divider orientation="vertical" flexItem />}>
                {
                  gameInfo?.chain?.map((item: any, index: number) => {
                    return index < 4 ? <Chip
                      key={index}
                      sx={{ margin: { xs: '5px 0 5px 0' }, backgroundColor: '#00000003' }}
                      avatar={<Avatar alt={item.name} src={item.icon} />}
                      label={item.name}
                    /> : <></>
                  })
                }

              </Stack>
              {/* 开始游戏以及新手引导 */}
              <Box className="step-url">
                <Box className="step" sx={{ margin: { xs: '10px 0 10px 0' } }}>
                  {
                    gameInfo?.game_url && <Link href={gameInfo?.game_url} sx={{
                      padding: {
                        md: '13px 47px', xs: '0.5rem 1rem',
                        fontSize: { xs: '0.875rem' }
                      },
                      mr: { md: '23px', xs: '0.5rem' }
                    }}>开始游戏</Link>
                  }
                  {gameInfo?.guide && <Link href={gameInfo?.guide} sx={{ padding: { md: '13px 47px', xs: '0.5rem 1rem', fontSize: { xs: '0.875rem' } } }}>新手引导</Link>}
                </Box>
                <Box className="url" sx={{ margin: { xs: '10px 0 10px 0' } }}>
                  <Link href={gameInfo?.twitter}> <Box component='img' src={require('@/assets/images/icons/twitter-details.svg')} alt='' sx={{ mr: { md: '16px', xs: 0 } }} /></Link>
                  <Link href={gameInfo?.facebook}> <Box component='img' src={require('@/assets/images/icons/facebook.svg')} alt='' sx={{ mr: { md: '16px', xs: 0 } }} /></Link>
                  <Link href={gameInfo?.telegram}> <Box component='img' src={require('@/assets/images/icons/telagram-details.svg')} alt='' sx={{ mr: { md: '16px', xs: 0 } }} /></Link>
                  <Link href={gameInfo?.youtube}> <Box component='img' src={require('@/assets/images/icons/youtu.svg')} alt='' /></Link>
                </Box>
              </Box>
            </CardContent>
          </Grid>
        </Grid>


        {/* 下面  */}
        <Grid container className="games-details-symbol">
          {gameInfo?.currency?.map((item: any) => (
            <Grid item key={item.id} xs={12} sm={6} md={6} sx={{ padding: { md: '0' }, mt: { md: '20px' } }}>
              <Card sx={{ height: '100%', display: 'flex', flexDirection: 'column' }} className='games-introduction-currency'>
                <Box sx={{ padding: { md: '12px 24px', xs: '0.5rem 1rem' }, background: '#26272B', display: 'flex', alignContent: 'center', alignItems: 'center' }}>
                  <CardMedia
                    component="img"
                    image={item?.logo}
                    alt="random"
                    sx={{ width: { md: '30px', xs: '2rem' }, display: 'inline-block', mr: { md: '14px', xs: '0.8rem' } }}
                  />
                  <Typography component="span" sx={{ fontSize: { md: "20px" }, color: "#F4F4F4", fontWeight: 'bold' }}>{item.currency_name?.toUpperCase()}</Typography>
                </Box>
                <Divider />
                <CardContent sx={{ flexGrow: 1, background: '#26272B', padding: { md: '12px 24px', xs: '0.5rem 1rem' } }}>
                  <Box className='games-introduction-symbol'>
                    <Typography component="span"> 最大供应量</Typography>
                    <Typography component="span"> {thousands2(item?.max_amount)} {item.currency_name?.toUpperCase()}</Typography>
                  </Box>
                  <Box className='games-introduction-symbol'>
                    <Typography component="span"> 流通量</Typography>
                    <Typography component="span"> {thousands2(item?.flow_amount)} {item.currency_name?.toUpperCase()}</Typography>
                  </Box>
                  <Box className='games-introduction-symbol'>
                    <Typography component="span"> 发行时间</Typography>
                    <Typography component="span"> {item?.issue_at ? formatTime(item?.issue_at) : ''}</Typography>

                  </Box>
                  <Box className='games-introduction-symbol'>
                    <Typography component="span"> 合约地址</Typography>
                    <Typography component="span"> {item?.address}</Typography>
                  </Box>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>
      </>
      <TableInfo gameInfo={gameInfo} />
      <Like gameInfo={gameInfo} />
      <Community />
    </Container>
  )
})

export default withRouter(GameIntroduction)