import React, { memo } from 'react';
import { Box, Tabs, Tab, Typography } from "@mui/material";
import { ArticleFirst } from '@/components'

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Typography sx={{ p: 3 }}>
          {children}
        </Typography>
      )}
    </div>
  );
}

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const TableInfo: React.FC<any> = memo(({gameInfo}:any) => {
  const [value, setValue] = React.useState(0);
  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  return (
    <Box sx={{ width: '100%', mt: { md: '24px', xs: '2rem' }, border:'1px solid #3D3E45', borderRadius:{md:'12px'} }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} textColor='inherit' aria-label="basic tabs example">
          <Tab sx={{ lineHeight: '12px', fontSize: '1rem', fontWeight: 550, color: '#D8D8D8' }} label="About Game" {...a11yProps(0)} />
          <Tab sx={{ lineHeight: '12px', fontSize: '1rem', fontWeight: 550, color: '#D8D8D8' }} label="News" {...a11yProps(1)} />
          <Tab sx={{ lineHeight: '12px', fontSize: '1rem', fontWeight: 550, color: '#D8D8D8' }} label="Stragegy" {...a11yProps(2)} />
          <Tab sx={{ lineHeight: '12px', fontSize: '1rem', fontWeight: 550, color: '#D8D8D8' }} label="Revenue Analysis" {...a11yProps(3)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
          <Box maxWidth='lg' component='span' sx={{'& video':{maxWidth:'100%'}}} dangerouslySetInnerHTML={{__html: gameInfo?.about_games}}/>
      </TabPanel>
      <TabPanel value={value} index={1}>
      <ArticleFirst list={gameInfo?.new || []} width={80} type='lastGames' />
      </TabPanel>
      <TabPanel value={value} index={2}>
          <Box maxWidth='lg' component='span' sx={{'& video':{maxWidth:'100%'}}} dangerouslySetInnerHTML={{__html: gameInfo?.stragegy}}/>
      </TabPanel>
      <TabPanel value={value} index={3}>
          <Box maxWidth='lg' component='span' sx={{'& video':{maxWidth:'100%'}}} dangerouslySetInnerHTML={{__html: gameInfo?.revenue_analysis}}/>
      </TabPanel>
    </Box>
  )
})

export default TableInfo
