import React, { memo, useEffect, useState, useMemo } from 'react';
import { Chip, Box, Typography, Container, Breadcrumbs, Link } from '@mui/material';
import { withRouter } from 'react-router-dom'
import GEN_API from '@/api/fetch'
import TypesTable from "./TypesTable";
import { Pagination } from 'antd';
import { parseSearch } from '@/utils'
import NavigateNextIcon from '@mui/icons-material/NavigateNext';

const Classification: React.FC<any> = memo(({ location: { search } }) => {
  const { type } = parseSearch(search)
  const [gTypes, setGTypes] = useState<any>([])
  const [cTypes, setCTypes] = useState<any>([])
  const [params, setParams] = useState<any>({ class_id: 0, chain_id: 0, status: 0 })
  const [list, setList] = useState<any>([])

  const status = useMemo(() => [
    { name: '所有', status: 0 },
    { name: '测试中', status: 1 },
    { name: '正在运营', status: 2 },
    { name: '开发中', status: 3 },
    { name: '预售中', status: 4 },
  ], [])

  useEffect(() => {
    (async () => {
      try {
        const { data } = await GEN_API.fetGamesType()
        setGTypes(data || [])
        const res = await GEN_API.fetChainType()
        setCTypes(res?.data || [])
      } catch (err) {
        console.log('错误信息', err)
      }
    })()
  }, [])

  // 获取游戏
  useEffect(() => {
    (async () => {
      try {
        const { data } = await GEN_API.fetGameValue(params)
        setList(data || [])
      } catch (err) {
        console.log('错误信息', err)
      }
    })()
  }, [params])

  return (
    <Container maxWidth="lg">
      <Breadcrumbs
        sx={{
          background: '#232427', borderRadius: { md: '12px', xs: '0.5rem' },
          padding: { md: '12px 24px', xs: '0.875rem 1.4rem' },
          margin: { md: '17px 0 24px', xs: '0.9rem 0 1.4rem' },
        }}
        separator={<NavigateNextIcon fontSize="small" />} aria-label="breadcrumb">
        [
        <Link key="1" href="/"
          sx={{ color: '#999', fontSize: { xs: '0..875rem' }, '&:hover': { color: '#999' } }}
        >Home</Link>,
        <Typography key="2" sx={{ color: '#FFF', fontSize: { xs: '0..875rem' } }}>
          {type.includes('games') ? '游戏分类' : 'Popular Games'}
        </Typography>,
        ];
      </Breadcrumbs>
      <>
        <Box component='div'>
          <Typography component='span'
            sx={{
              fontSize: { md: '15px', xs: '0.875rem' },
              fontWeight: 'bold', color: '#FFF'
            }}>
            类型：
          </Typography>
          {
            gTypes?.map((item: any) => {
              return (
                <Chip label={item?.class} variant="outlined"
                  key={item.id}
                  className={(params.class_id === item.id) ? 'active' : ''}
                  onClick={() => setParams((prevState: any) => ({
                    ...prevState,
                    class_id: item?.id
                  }))}
                  sx={{
                    margin: { md: '8px 6px', xs: '0.5rem' },
                    borderRadius: '6px'
                  }} />
              )
            })
          }
        </Box>
        <Box component='div'>
          <Typography component='span'
            sx={{
              fontSize: { md: '15px', xs: '0.875rem' },
              fontWeight: 'bold', color: '#FFF'
            }}
          >
            公链：
          </Typography>
          {
            cTypes?.map((item: any) => {
              return <Chip label={item?.name} variant="outlined"
                key={item.id}
                className={(params.chain_id === item.id) ? 'active' : ''}
                onClick={() => setParams((prevState: any) => ({
                  ...prevState,
                  chain_id: item?.id
                }))}
                sx={{
                  margin: { md: '8px 6px', xs: '0.5rem' },
                  borderRadius: '6px'
                }} />
            })
          }
        </Box>
        <Box component='div'>
          <Typography component='span'
            sx={{
              fontSize: { md: '15px', xs: '0.875rem' },
              fontWeight: 'bold', color: '#FFF'
            }}>
            状态：
          </Typography>
          {
            status?.map((item: any) => {
              return <Chip label={item?.name} variant="outlined"
                key={item.status}
                className={(params.status === item.status) ? 'active' : ''}
                onClick={() => setParams((prevState: any) => ({
                  ...prevState,
                  status: item?.status
                }))}
                sx={{
                  margin: { md: '8px 6px', xs: '0.5rem' },
                  borderRadius: '6px'
                }} />
            })
          }
        </Box>
      </>

      <Box sx={{ width: '100%', mt: { md: '24px', xs: '2rem' }, border: '1px solid #3D3E45', borderRadius: { md: '12px' } }}>
        <TypesTable games={list} type={type}/>
      </Box>

      <Pagination className='my-pagination' defaultCurrent={1} total={list?.length} showQuickJumper />

    </Container>

  );
})

export default withRouter(Classification)