import React, { memo, useEffect, useState, useCallback } from 'react'
import { withRouter, useHistory } from 'react-router-dom'
import { Grid, CardContent, Typography, Card, Breadcrumbs, Link, Container, Paper, Stack } from '@mui/material';
import { formatTime, parseSearch } from '@/utils'
import { Community } from '@/components'
import { ARTICLE_TYPE } from '@/config'
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import GEN_API from '@/api/fetch'
import { styled } from '@mui/material/styles';

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const Detail: React.FC<any> = memo(({ location: { search } }) => {
  const history = useHistory()
  const { cate_id, id, type } = parseSearch(search)
  const [details, setDetails] = useState<any>({ curArticle: {}, otherList: [] })

  useEffect(() => {
    console.log('详情页面', parseSearch(search))
  }, [search])

  useEffect(() => {
    (async () => {
      try {
        const { data } = await GEN_API.queryArticle({ id })
        const list = await GEN_API.queryRelated({ id, cate_id })
        setDetails((prevState: any) => (
          {
            ...prevState,
            curArticle: data ? data[0] : {},
            otherList: list?.data || []
          }
        ))
      } catch (err) {
        console.log('错误信息', err)
      }
    })()
  }, [id, cate_id])

  const refreshPage = useCallback((article: any) => {
    history.push(`/details?type='news'&cate_id=${cate_id}&id=${article.id}`)
    setDetails((prevState: any) => (
      {
        ...prevState,
        curArticle: article
      }
    ))
  }, [history, cate_id])

  return (
    <>
      <Container maxWidth="lg" className="total-news">
        <Breadcrumbs
          sx={{
            background: '#232427', borderRadius: { md: '12px', xs: '0.5rem' },
            padding: { md: '12px 24px', xs: '0.875rem 1.4rem' },
            margin: { md: '17px 0 24px', xs: '0.9rem 0 1.4rem' },
          }}
          separator={<NavigateNextIcon fontSize="small" />} aria-label="breadcrumb">
          [
          <Link key="1" href="/"
            sx={{ color: '#999', fontSize: { xs: '0..875rem' }, '&:hover': { color: '#999' } }}
          >Home
          </Link>,
          <Link
            key="2"
            sx={{ color: '#999', fontSize: { xs: '0..875rem' }, '&:hover': { color: '#999' } }}
            href={type.includes('news') ? '/news' : type.includes('ranking') ? '/latestGames' : '/university'}
          >
            {type.includes('news') ? 'News' : type.includes('ranking') ? 'Latest Games' : 'BG University'}
          </Link>,
          {/* 只有BG */}
          {console.log(type, Object.prototype.toString.call(type).slice(8, -1).toLowerCase())}

          {type && (!type.includes('news')) && (!type.includes('ranking')) &&  <Link
            key="3"
            sx={{ color: '#999', fontSize: { xs: '0..875rem' }, '&:hover': { color: '#999' } }}
            href={+type === ARTICLE_TYPE.GRAPHIC ? '/graphic' : '/video'}
          >
            {+type === ARTICLE_TYPE.GRAPHIC ? '图文教程' : '视频教程'}
          </Link>}
          <Typography key="3" sx={{ color: '#FFF', fontSize: { xs: '0..875rem' } }}>
            <span dangerouslySetInnerHTML={{ __html: details.curArticle?.title }} />
          </Typography>,
          ];
        </Breadcrumbs>
        <Grid container spacing={5} sx={{ mt: 3 }}>
          {/* 左侧文章详情 */}
          <Grid xs={12} md={8} sx={{ pr: { md: '16px' } }}>
            <Typography variant="h4" gutterBottom component="div">
              {details.curArticle?.title}
            </Typography>
            <Typography variant="caption" display="block" gutterBottom>
              {details.curArticle?.created ? formatTime(details.curArticle?.created) : ''}
            </Typography>
            <Stack direction="row" spacing={2} sx={{ margin: { xs: '5px 0 5px 0 ', md: '10px 0 10px 0 ' } }}>
              {
                details.curArticle?.label?.map((item: any, index: number) => {
                  return <Item key={index}>{item.word}</Item>
                })
              }
            </Stack>
            <div dangerouslySetInnerHTML={{ __html: details.curArticle?.rich_text }} />
          </Grid>
          <Grid xs={12} md={4}>
            <Typography noWrap sx={{ fontSize: { md: '28px', xs: '1.125rem' }, pb: '10px' }}>
              {/* {type.includes('news') ? ' 24H热闻' : type.includes('ranking') ? '其他文章' : '其他教程'} */}
              {type.includes('ranking') ? '其他文章' : '其他教程'}
            </Typography>
            <div className="content">
              {details.otherList?.slice(0, 5)?.map((item: any) => {
                return (
                  <>
                    <Card
                      className='text-container'
                      sx={{
                        p: { md: '16px', xs: '1rem' },
                        '&:hover': {
                          background: '#26272B',
                        }
                      }}
                      onClick={() => refreshPage(item)}
                    >
                      <CardContent sx={{ maxWidth: '100%', width: '100%', padding: '0' }}>
                        <Typography className='title'>{item?.title}</Typography>
                        <Typography noWrap sx={{ color: '#999', mt: { paddingTop: '12px' } }}>{item?.created ? formatTime(item?.created) : ''}</Typography>
                      </CardContent>
                    </Card>
                  </>);
              })}
            </div>
          </Grid>
        </Grid>
      </Container>
      <Container>
        <Community />
      </Container>
    </>
  )
})

export default withRouter(Detail)