import React, { memo } from 'react';
import ExpandLess from "@mui/icons-material/ExpandLess";
import ExpandMore from "@mui/icons-material/ExpandMore";
import IconButton from "@mui/material/IconButton";
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import { Link } from '@mui/material';

interface dropButtonProps {
  title: string;
  subItem: ReadonlyArray<{
    title: string;
    href: string;
  }>;
}
const Dropbutton: React.FC<dropButtonProps> = memo((props: dropButtonProps) => {
  const { subItem, title } = props;
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <>
      <IconButton
        onClick={handleClick}
        sx={{ fontSize: '1rem', pl: '35px' }}
        disableRipple
        disableFocusRipple
      >
        {title}
        {open ? <ExpandLess /> : <ExpandMore />}
      </IconButton>
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}
      >
        {subItem.map((item, index) => {
          return <Link key={index} href={item.href}><MenuItem sx={{ minWidth: '100px',  color:'#FFF', textDecoration:'none', '&:hover': {
            background: 'inherit'
          }}}>{item.title}</MenuItem></Link>
        })}
      </Menu>
    </>
  );
})

export default Dropbutton