import React, { memo, useEffect, useState } from 'react'
import { Carousel as AntdCarousel } from 'antd';
import { CardHeader, Typography, IconButton } from "@mui/material";
import { useHistory } from 'react-router-dom'
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import GEN_API from '@/api/fetch'
import './style.scss'


const Carousel: React.FC<any> = memo(({ isMobile }: any) => {
  const history = useHistory()
  const [banners, setBanners] = useState<any>([])

  useEffect(() => {
    (async () => {
      try {
        const { data } = await GEN_API.fetCourse()
        setBanners(data || [])
      } catch (err) {
        console.log('错误信息', err)
      }
    })()
  }, [])


  return (
    <div className="banner-list">
      <CardHeader
        disableTypography
        action={
          <IconButton href="/university" size='small' sx={{ display: { md: 'flex', xs: "none" }, fontSize: '14px', fontWeight: 'bold', color: '#999999', mt: '8px', '&:hover': { color: '#FFF' } }} disableRipple disableFocusRipple>
            More
            <ChevronRightIcon />
          </IconButton>
        }
        title={<Typography variant="button" display="block" component='p' sx={{ fontSize: { md: '28px', xs: '1.125rem' }, fontWeight: 'bold', color: '#FFF' }}>
          从入门到熟练精通
        </Typography>}
        subheader={
          <Typography variant="button" display="block" component='span' sx={{ fontSize: '0.875rem', fontWeight: 'bold', color: '#999999' }}>
            学习相关知识，熟悉区块链运作，小白成为大佬
          </Typography>
        }
        sx={{ pt: { xs: '2.5rem', md: '100px' }, pb: { xs: '1.25rem', md: '40px' }, pl: 0 }}
      />
      {
        !isMobile ? <>
          <AntdCarousel
            className="banner"
            dots={isMobile ? true : false}
            infinite={true}
            variableWidth={true}
            slidesToShow={isMobile ? 2 : 3}
            slidesToScroll={1}
            autoplaySpeed={5000}
            arrows={true}
          >
            {
              banners?.slice(0, 6)?.map((item: any) => {
                return (<div key={item?.id} onClick={() => history.push(`/details?type=${+item?.category?.parent_id}&cate_id=${item?.cate_id}&id=${item.id}`)}>
                  <img src={item?.cover} alt="banner" />
                </div>)
              })
            }
          </AntdCarousel>
          <IconButton href='/university' size='small' sx={{ display: { md: 'none', xs: "flex" }, width: '100%', justifyContent: 'flex-end', fontSize: '0.875rem', fontWeight: 'bold', color: '#999', '&:hover': { color: '#FFF' } }} disableRipple disableFocusRipple>
            More
            <ChevronRightIcon />
          </IconButton>
        </> :
          <><div className="assets-box">
            {
              banners?.slice(0, 6).map((item: any) => {
                return <div key={item?.id} className="assets-url" onClick={() => history.push(`/details?type=${+item?.category?.parent_id}&cate_id=${item?.cate_id}&id=${item.id}`)}>
                  <img src={item?.cover} alt="" />
                </div>
              })
            }
          </div>
            <IconButton href="/" size='small' sx={{ mt: '-1rem', width: '100%', fontSize: '0.875rem', fontWeight: 'bold', color: '#999999', '&:hover': { color: '#FFF' }, justifyContent: 'flex-end' }} disableRipple disableFocusRipple>
              More
              <ChevronRightIcon />
            </IconButton>
          </>
      }
    </div>
  )
})
export default Carousel