import React, { useCallback } from 'react';
import ImageListItemBar from '@mui/material/ImageListItemBar';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import { Box } from "@mui/material";
import { Carousel } from 'antd';
import Divider from '@mui/material/Divider';
import { useLightContext } from '@/hooks'
import { useHistory } from 'react-router-dom'
import { ARTICLE_TYPE } from '@/config'
import './style.scss'

function srcset(image: string, size: number, rows = 1, cols = 1) {
  return {
    src: `${image}?w=${size * cols}&h=${size * rows}&fit=crop&auto=format`,
    srcSet: `${image}?w=${size * cols}&h=${size * rows
      }&fit=crop&auto=format&dpr=2 2x`,
  };
}

export default function Banner() {
  const history = useHistory()
  const { banner }: any = useLightContext()

  const goDetails = useCallback((item: any) => {
    if (item?.parent_id === 1) {
      switch (item?.cade_id) {
        case ARTICLE_TYPE.HOT:
        case ARTICLE_TYPE.HAPPENING:
          history.push(`/details?type='news'&cate_id=${item.cade_id}&id=${item.id}`);
          break;
        case ARTICLE_TYPE.POTENTIAL:
          history.push(`/details?type='ranking'&cate_id=${item.cade_id}&id=${item.id}`);
          break;
      }
    } else {
      history.push(`/details?type=${+item?.category?.parent_id}&cate_id=${item?.cade_id}&id=${item?.id}`)
    }
  }, [history])

  return (
    <>
      <Box maxWidth="lg" sx={{ display: { xs: 'none', md: 'block' } }}>
        <ImageList
          sx={{ height: 460, pt: '53px', pb: '53px', mb: '0px', overflow: 'visible' }}
          variant="quilted"
          cols={4}
          rowHeight={164}
          key={1}
        >
          {banner?.map((item: any, index: number) => (
            index === 0 ? <ImageListItem
              onClick={() => goDetails(item)}
              key={index}
              cols={2}
              rows={2}
              sx={{
                mt: '10px', mr: '10px', minHeight: '344px', '&:hover': {
                  md: {
                    transform: 'scale(1.02) translateY(-6px)',
                    boxShadow: '0px 26px 40px -24px #000000ab',
                    transition: 'all 0.2s linear',
                    borderRadius: '.75rem'
                  },
                  transition: 'all 0.2s linear',
                  zIndex: 'tooltip'
                }
              }}
            >
              <img
                className='banner-img-radius'
                {...srcset(item?.cover, 140, item?.rows, item?.cols)}
                alt=''
                loading="lazy"
              />
              <ImageListItemBar
                sx={{
                  background: 'linear-gradient(to top, rgba(0,0,0,1) 0%,rgba(0,0,0,0.5) 70%, rgba(0,0,0,0) 100%)',
                  borderRadius: '0 0 .75rem .75rem'
                }}
                title=''
              // title={item?.title}
              />
            </ImageListItem> : <ImageListItem
              onClick={() => goDetails(item)}
              key={index}
              cols={1}
              rows={1}
              sx={{
                mt: '10px', ml: '10px', '&:hover': {
                  md: {
                    transform: 'scale(1.02) translateY(-6px)',
                    boxShadow: '0 26px 40px -24px #000000ab',
                    transition: 'all 0.2s linear',
                    borderRadius: '.75rem'
                  }
                },
                transition: 'all 0.2s linear'
              }}
            >
              <img
                className='banner-img-radius'
                {...srcset(item?.cover, 140, item?.rows, item?.cols)}
                alt={item?.title}
                loading="lazy"
              />
              <ImageListItemBar
                sx={{
                  background: 'linear-gradient(to top, rgba(0,0,0,1) 0%,rgba(0,0,0,0.5) 70%, rgba(0,0,0,0) 100%)',
                  borderRadius: '0 0 .75rem .75rem'
                }}
                title=''
              // title={item?.title}
              />
            </ImageListItem>
          ))}
        </ImageList>
        <Divider sx={{ mt: '10px', mb: '50px' }} />
      </Box>
      <Box sx={{ display: { xs: 'block', md: 'none', borderRadius: '.75rem' } }} component="div">
        <Carousel autoplay className='banner-carousel-mobile'>
          {banner?.map((item: any, index: number) => {
            return <img
              onClick={() => goDetails(item)}
              className='banner-img-mobile'
              src={`${item?.cover}`}
              key={index}
              alt=''
              // alt={item?.title}
              loading="lazy"
            />
          })}
        </Carousel>
      </Box>
    </>
  );
}