import React, { memo, useState, useEffect } from 'react'
import { Container, Breadcrumbs, Link, Typography, Grid, Card, CardMedia, Chip } from '@mui/material';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import GEN_API from '@/api/fetch'
import { Pagination } from 'antd';
import { useHistory } from 'react-router-dom'
import { ARTICLE_TYPE } from '@/config'

const Graphic: React.FC<any> = memo(() => {
  const history = useHistory()
  const [graphics, setGraphic] = useState<any>([])
  const [curPage, setCurPage] = useState<number>(1)
  const [collection, setCollection] = useState<any>([])
  const [params, setParams] = useState<any>(0)

// 获取分类
useEffect(() => {
  (async () => {
    try {
      const { data } = await GEN_API.fetCategory({parent_id:ARTICLE_TYPE.GRAPHIC})
      console.log('data', data)
      setCollection(data || [])
    } catch (err) {
      console.log('错误信息', err)
    }
  })()
}, [])

//  获取表格数据
  useEffect(() => {
    (async () => {
      try {
        const res = await GEN_API.fetGraphic({ page_size: 12, page: curPage, cate_id: params})
        setGraphic(res)
      } catch (err) {
        console.log('错误信息', err)
      }
    })()
  }, [curPage, params])

  return (
    <Container maxWidth="lg">
      <Breadcrumbs
        sx={{
          background: '#232427', borderRadius: { md: '12px', xs: '0.5rem' },
          padding: { md: '12px 24px', xs: '0.875rem 1.4rem' },
          margin: { md: '17px 0 24px', xs: '0.9rem 0 1.4rem' },
        }}
        separator={<NavigateNextIcon fontSize="small" />} aria-label="breadcrumb">
        [
        <Link key="1" href="/"
          sx={{ color: '#999', fontSize: { xs: '0..875rem' }, '&:hover': { color: '#999' } }}
        >Home</Link>,
        <Link key="1" href="/university"
          sx={{ color: '#999', fontSize: { xs: '0..875rem' }, '&:hover': { color: '#999' } }}
        >BG University</Link>,
        <Typography key="2" sx={{ color: '#FFF', fontSize: { xs: '0..875rem' } }}>
          图文教程
        </Typography>,
        ];
      </Breadcrumbs>
      {
        collection?.map((item: any) => {
          return <Chip label={item?.name} variant="outlined"
            key={item.status}
            className={(params === item.id) ? 'active' : ''}
            onClick={() => setParams(item?.id)}
            sx={{
              margin: { md: '10px 6px 20px', xs: '0.5rem' },
              borderRadius: '6px'
            }} />
        })
      }

      <Grid container spacing={4}>
        {graphics?.data?.map((item: any) => (
          <Grid item key={item?.id} xs={6} sm={4} md={3}
            onClick={() => history.push(`/details?type=${+item.category?.parent_id}&cate_id=${item?.cate_id}&id=${item.id}`)}
          >
            <Card
              sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
            >
              <CardMedia
                component="img"
                image={item?.cover}
                alt="random"
                sx={{ borderRadius: { md: '10px', xs: '0.6rem' } }}
              />
              <Typography gutterBottom variant="h5" component="div" sx={{
                color: '#FFF', margin: { md: '14px 0 14px 6px', xs: '1rem 0 1rem 0.5rem' },
                fontSize: { xs: '0.875rem' }
              }}>{item?.title}</Typography>
            </Card>
          </Grid>
        ))}
      </Grid>
      <Pagination className='my-pagination'
        defaultCurrent={1}
        total={graphics?.article_num}
        pageSize={12}
        showQuickJumper
        hideOnSinglePage={true}
        onChange={(e: any) => setCurPage(e)} />

    </Container>
  )
})

export default Graphic